import {reqCartList,reqDeleteCartById,reqUpdateCheckedById} from "@/api"

const state={
    cartList:[],
    deleteCartListBySkuId:[],
}
const actions={
    // 
    async getCartList({commit}){
        let result=await reqCartList()
        // 
        // console.log(result);
        if(result.code==200){
            commit('GETCARTLIST',result.data)
        }
    },
    // 
    async deleteCartListBySkuId({commit},skuId){
        let result=await reqDeleteCartById(skuId)
        if(result.code==200){
           return 'ok'
        }else{
            return Promise.reject(new Error("faile"))
        }
    },
    // 
    async UpdateCheckedById({commit},{skuId,isChecked}){
        let result=await reqUpdateCheckedById(skuId,isChecked)
        if(result.code==200){
            return 'ok'
        }else{
            return Promise.reject(new Error("faile"))
        }
    },
    // 
    deleteAllCheckedCart({dispatch,getters}){
    //    console.log(context);
    // context:commit{mutationsstate} getters{} dispatch{action} state{}
    // console.log(getters);
    // 
       let PromiseAll=[]
        getters.cartList.cartInfoList.forEach(item=>{
        // promise
        let promise=item.isChecked==1?dispatch('deleteCartListBySkuId',item.skuId):''
        // Promise
        PromiseAll.push(promise)
    })
    // p1|p2...
    // 
    return Promise.all(PromiseAll)
    },
    // 
    updateAllCartChecked({dispatch,state},isChecked){
    //    console.log(state);
    let promiseAll=[]
    state.cartList[0].cartInfoList.forEach((item)=>{
        let promise=dispatch("UpdateCheckedById",{skuId:item.skuId,isChecked})
        promiseAll.push(promise)
    })
    // 
    return Promise.all(promiseAll)
    }
}
const mutations={
    GETCARTLIST(state,cartList){
        state.cartList=cartList
    },
    DELETECARTLISTBYSKUID(state,deleteCartListBySkuId){
        state.deleteCartListBySkuId=deleteCartListBySkuId
    }
}
const getters={
    cartList(state){
        return state.cartList[0]||{}
    },
   /*  // 
    cartInfoList(state){
        return state.cartList.cartInfoList||{}
    } */
}

export default{
    state,
    actions,
    mutations,
    getters
}
