// 
// 
import Home from '@/pages/Home'
import Login from '@/pages/Login'
import Register from '@/pages/Register'
import Search from '@/pages/Search'
import Detail from '@/pages/Detail'
import AddCartSuccess from '@/pages/AddCartSuccess'
import ShopCart from '@/pages/ShopCart'
import Trade from '@/pages/Trade'
import Pay from '@/pages/Pay'
import PaySuccess from '@/pages/PaySuccess'
// 
import Center from '@/pages/Center'
// 
import myOrder from '@/pages/Center/groupOrder'
import groupOrder from '@/pages/Center/myOrder'


//
export default
    //
[
    {
        path:'/home',
        component:Home,
        meta:{isShow:true}
    },
    {
        path:'/login',
        component:Login,
        meta:{isShow:false}
    },
    {
        path:'/register',
        component:Register,
        meta:{isShow:false}
    },
    {
        path:'/search/:keyword?',
        component:Search,
        meta:{isShow:true},
        name:'search',
        // props
        // :params
        // props:true,
        // props
        // props:{a:1,b:2}
        //paramsqueryprops
        /* props:($route)=>{
            return {keyword:$route.params.keyword,k:$route.query.k}
        }  */

    },
    {
      path:'/detail/:skuid',
      component:Detail,
      meta:{isShow:true},
      name:"detail"
    },
    {
        path:'/addcartsuccess',
        name:"addcartsuccess",
        component:AddCartSuccess,
        meta:{isShow:true}
    },
    {
        path:'/shopcart',
        name:"shopcart",
        component:ShopCart,
        meta:{isShow:true}
    },
    {
        path:'/trade',
        name:"trade",
        component:Trade,
        meta:{isShow:true},
        // 
        beforeEnter:(to,from,next)=>{
            //shopcart
            if(from.pach=='/shopcart'){
                next()
            }else{
                // 
                next(false)
            }
        }
    },
    {
        path:'/pay',
        name:'pay',
        component:Pay,
        meta:{isShow:true},
        // 
        beforeEnter:(to,from,next)=>{
        //   
        if(from.path=='/trade'){
            next()
        }else{
            next(false)
        }
        }
    },
    {
        path:'/paysuccess',
        name:'paysuccess',
        component:PaySuccess,
        meta:{isShow:true}
    },
    {
        path:'/center',
        component:Center,
        meta:{isShow:true},
        // 
        children:[
            {
                path:'myorder',
                name:'myorder',
                component:myOrder
            },
            {
                path:'grouporder',
                name:'grouporder',
                component:groupOrder
            },
            // 
            {
                path:'center',
                redirect:'center/myorder'
            }
        ]
    },
    //  
    {
        path:'*',
        redirect:'/home'
    }
]
