<template>
  <div class="pay-main">
    <el-button type="primary" icon="el-icon-edit"></el-button>
    
    <div class="pay-container">
      <div class="checkout-tit">
        <h4 class="tit-txt">
          <span class="success-icon"></span>
          <span class="success-info">~~</span>
        </h4>
        <div class="paymark">
          <span class="fl"><em class="orange time">4</em><em>{{orderId}}</em></span>
          <span class="fr"><em class="lead"></em><em class="orange money">{{payInfo.totalFee}}</em></span>
        </div>
      </div>
      <div class="checkout-info">
        <h4></h4>
        <ol>
          <li><span class="zfb"></span></li>
          <li></li>
          <li></li>
        </ol>
        <h4><span class="save"></span></h4>
        <ul>
          <li>11111111</li>
          <li>111111</li>
          <li>111111</li>
        </ul>
      </div>
      <div class="checkout-steps">
        <div class="step-tit">
          <h5></h5>
        </div>
        <div class="step-cont">
          <ul class="payType">
            <li><img src="./images/pay2.jpg"></li>
            <li><img src="./images/pay3.jpg"></li>
          </ul>

        </div>
        <div class="hr"></div>

        <div class="payshipInfo">
          <div class="step-tit">
            <h5></h5>
          </div>
          <div class="step-cont">
            <ul class="payType">
              <li><img src="./images/pay10.jpg"></li>
              <li><img src="./images/pay11.jpg"></li>
              <li><img src="./images/pay12.jpg"></li>
              <li><img src="./images/pay13.jpg"></li>
              <li><img src="./images/pay14.jpg"></li>
              <li><img src="./images/pay15.jpg"></li>
              <li><img src="./images/pay16.jpg"></li>
              <li><img src="./images/pay17.jpg"></li>
              <li><img src="./images/pay18.jpg"></li>
              <li><img src="./images/pay19.jpg"></li>
              <li><img src="./images/pay20.jpg"></li>
              <li><img src="./images/pay21.jpg"></li>
              <li><img src="./images/pay22.jpg"></li>

            </ul>
          </div>

        </div>
        <div class="hr"></div>

        <div class="submit">
          <!-- <router-link class="btn" to="/paysuccess"></router-link> -->
          <a class="btn" @click="open"></a>
        </div>
        <div class="otherpay">
          <div class="step-tit">
            <h5></h5>
          </div>
          <div class="step-cont">
            <span><a href="weixinpay.html" target="_blank"></a></span>
            <span></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QRCode from 'qrcode'
  export default {
    name: 'Pay',
    data(){
      return{
        payInfo:{},
        timer:null
      }
    },
    computed:{
      orderId(){
        return this.$route.query.orderId
      }
    },
    mounted(){
     this.getPayInfo()
    },
    methods:{
      // 
      async getPayInfo(){
       let result= await this.$API.reqPayInfo(this.orderId)
       console.log(result);
      // 
      if(result.code==200){
        this.payInfo=result.data
      }
      },
      // 
     async open(){
        // 
         let url= await QRCode.toDataURL(this.payInfo.codeUrl)
        //  console.log(url);//promise ,      
        this.$alert(`<img src=${url}>`, '', {
          //  message  HTML 
          dangerouslyUseHTMLString: true,
          // 
          center:true,
          // 
          showConfirmButton:true,
          // 
          showCancelButton:true,
          // 
          confirmButtonText:'',
          // 
          cancelButtonText:'',
          // MessageBox 
          showClose:false,
          // 
          beforeClose:(type,Instance,done)=>{
            // type:|
            // instance
            // done
            if(type=='cancel'){
              alert('0001111')
              clearInterval(this.timer)
              this.timer=null
              // 
              done()
            }else{
              // 
              // if(this.code==200){
                clearInterval(this.timer)
                this.timer=null
                done()
               this.$router.push('/paysuccess')
              // }
            }
          }
        })
        // 
        // 
        // 
        if(!this.timer){
          this.timer=setInterval(async()=>{
            // 
           let result=await this.$API.reqPayStatus(this.orderId)
          //  console.log(result);
          if(result.code==200){
            // 
            clearInterval(this.timer)
            this.timer=null
            // code
            this.code=result.code
            // 
            this.$msgbox.close()
            // 
            this.$router.push('/paysuccess')
          }
          },1000)
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .pay-main {
    margin-bottom: 20px;

    .pay-container {
      margin: 0 auto;
      width: 1200px;

      a:hover {
        color: #4cb9fc;
      }

      .orange {
        color: #e1251b;
      }

      .money {
        font-size: 18px;
      }

      .zfb {
        color: #e1251b;
        font-weight: 700;
      }

      .checkout-tit {
        padding: 10px;

        .tit-txt {
          font-size: 14px;
          line-height: 21px;

          .success-icon {
            width: 30px;
            height: 30px;
            display: inline-block;
            background: url(./images/icon.png) no-repeat 0 0;
          }

          .success-info {
            padding: 0 8px;
            line-height: 30px;
            vertical-align: top;
          }
        }

        .paymark {
          overflow: hidden;
          line-height: 26px;
          text-indent: 38px;

          .fl {
            float: left;
          }

          .fr {
            float: right;

            .lead {
              margin-bottom: 18px;
              font-size: 15px;
              font-weight: 400;
              line-height: 22.5px;
            }
          }
        }
      }

      .checkout-info {
        padding-left: 25px;
        padding-bottom: 15px;
        margin-bottom: 10px;
        border: 2px solid #e1251b;

        h4 {
          margin: 9px 0;
          font-size: 14px;
          line-height: 21px;
          color: #e1251b;
        }

        ol {
          padding-left: 25px;
          list-style-type: decimal;
          line-height: 24px;
          font-size: 14px;
        }

        ul {
          padding-left: 25px;
          list-style-type: disc;
          line-height: 24px;
          font-size: 14px;
        }
      }

      .checkout-steps {
        border: 1px solid #ddd;
        padding: 25px;

        .hr {
          height: 1px;
          background-color: #ddd;
        }

        .step-tit {
          line-height: 36px;
          margin: 15px 0;
        }

        .step-cont {
          margin: 0 10px 12px 20px;

          ul {
            font-size: 0;

            li {
              margin: 2px;
              display: inline-block;
              padding: 5px 20px;
              border: 1px solid #ddd;
              cursor: pointer;
              line-height: 18px;
            }
          }
        }
      }

      .submit {
        text-align: center;

        .btn {
          display: inline-block;
          padding: 15px 45px;
          margin: 15px 0 10px;
          font: 18px "";
          font-weight: 700;
          border-radius: 0;
          background-color: #e1251b;
          border: 1px solid #e1251b;
          color: #fff;
          text-align: center;
          vertical-align: middle;
          cursor: pointer;
          user-select: none;
          text-decoration: none;
        }
      }
    }
  }
</style>